import * as React from 'react';
import * as Router from 'react-router-dom';
import logo from './logo.svg';
import './App.css';
import LoginForm from "./components/LoginForm";
import Admin from './layouts/Admin';
import { useAppDispatch, useAppSelector } from './hooks';
import { setRoomId } from './features/room/room-slice';


const App: React.FC<{}> = () => {
  let { roomId } = Router.useParams();
  const loggedIn = useAppSelector((state) => state.userR.loggedIn);
  const dispatch = useAppDispatch();
  if (roomId) {
    dispatch(setRoomId(roomId));
    if (!loggedIn) return (<LoginForm />);
    else return (
        <Admin />
    );
  } else {
    return (
      <div className='bg-primary bg-gradient text-white vh-100'>
        <h1>URLが不正です</h1>
      </div>
    );
  }
};

export default App;
