import * as React from 'react';
import * as Router from 'react-router-dom';
import * as RB from 'react-bootstrap';
import { paths, operations, components } from '../schema';
import fetcher from '../fetcher';
import { useAppDispatch, useAppSelector } from '../hooks';
import { resolved, setRoomId, setRoomName } from '../features/room/room-slice';


const CreateRoomForm: React.FC<{}> = () => {
    let navigate = Router.useNavigate();
    const roomName = useAppSelector((state) => state.roomR.roomName);
    const dispatch = useAppDispatch();
    const handleRoomName = (event: React.ChangeEvent<HTMLInputElement>) => {
        dispatch(setRoomName(event.target.value));
    };
    const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const createRoom = fetcher.path('/room/create').method('post').create();
        createRoom({ name: roomName }).then((res) => {
            let roomId = res.data as string;
            dispatch(setRoomId(roomId));
            navigate(`/${roomId}`);
        });
    };
    return (
        <RB.Container>
            <RB.Row>
                <RB.Col><h1> Creating a new room </h1></RB.Col>
            </RB.Row>
            <RB.Row>
                <RB.Col>
                    <RB.Form onSubmit={handleSubmit}>
                        <RB.Form.Group controlId='roomName'>
                            <RB.Form.Label>Enter the room name</RB.Form.Label>
                            <RB.Form.Control type="text" onChange={handleRoomName}></RB.Form.Control>
                        </RB.Form.Group>
                        <RB.Form.Group controlId='submit'>
                            <RB.Button type="submit" variant='success' active={!roomName.trim()}>CREATE</RB.Button>
                        </RB.Form.Group>
                    </RB.Form>
                </RB.Col>
            </RB.Row>
        </RB.Container>
    );
};

export default CreateRoomForm;