import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-icons/font/bootstrap-icons.css'; // needs additional webpack config!

import * as React from 'react';
import Container from 'react-bootstrap/Container';
import FullCalendar, { DateSelectArg, EventDropArg, EventSourceFunc, EventInput, EventClickArg, EventApi } from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin, { EventResizeDoneArg } from '@fullcalendar/interaction'; // for selectable
import { useAppSelector } from '../hooks';
import fetcher from '../fetcher';
import { Button, Modal, ModalDialog, Row } from 'react-bootstrap';
import TimezoneSelect, { ITimezone } from 'react-timezone-select';
import momentTimezonePlugin from '@fullcalendar/moment-timezone';
import moment from 'moment-timezone';
import * as TA from '../schedule-alg';

const UserProfile: React.FC<{}> = () => {
  const userId = useAppSelector((state) => state.userR.userId);
  const userName = useAppSelector((state) => state.userR.userName);
  const [eventSelected, setEventSelected] = React.useState<EventApi | null>(null);
  const [tz, setTZ] = React.useState<ITimezone>(Intl.DateTimeFormat().resolvedOptions().timeZone);
  const handleEditEvent = (info: EventDropArg | EventResizeDoneArg) => {
    const updateEvent = fetcher.path('/schedule/update').method('put').create();
    const event = info.event;
    updateEvent({ id: parseInt(event.id), user_id: userId, start: event.start!.toISOString(), end: event.end!.toISOString() });
  };
  const handleEventClick = (e: EventClickArg) => {
    setEventSelected(e.event);
  };
  const handleSelect = (selectionInfo: DateSelectArg) => {
    const registerEvent = fetcher.path('/schedule/register').method('post').create();
    const calendar = selectionInfo.view.calendar;
    const tmp_id = new Date().toString();
    const event = {
      id: tmp_id,
      userNames: [],
      start: selectionInfo.start,
      end: selectionInfo.end,
    };
    if (calendar.getEvents().map((e, i, a) => { return { userNames: [], start: e.start, end: e.end } as TA.TimeRange; }).filter((e, i, a) => !TA.isDisjoint(e, event)).length == 0) {
      registerEvent({ user_id: userId, start: selectionInfo.start.toISOString(), end: selectionInfo.end.toISOString() })
        .then((res) => {
          calendar.getEventById(tmp_id)?.setProp('id', (res.data as number).toString());
        });
      calendar.addEvent(event);
    }
  };
  const deleteEvent = () => {
    if (eventSelected) {
      const removeEvent = fetcher.path('/schedule/delete/{user_id}/{event_id}').method('delete').create();
      removeEvent({ user_id: userId, event_id: eventSelected.id }).then((res) => {
        eventSelected.remove();
      }).catch((e) => console.error(e))
        .finally(() => setEventSelected(null));
    }
  };
  const calendar = React.useRef<FullCalendar|null>(null);
  return (
    <>
      {eventSelected &&
        <Modal backdrop="static" show={true} fullscreen='sm-down' animation={false}>
          <Modal.Header>
            <Modal.Title>
              Delete this?
              <ul>
                <li>date: {eventSelected.start?.toLocaleDateString()}</li>
                <li>time: {eventSelected.start?.toLocaleTimeString()} to {eventSelected.end?.toLocaleTimeString()}</li>
              </ul>
            </Modal.Title>

          </Modal.Header>
          <Modal.Body>
            <Button variant='primary' onClick={() => { setEventSelected(null); }}>Keep</Button>
            <Button variant='danger' onClick={deleteEvent}>Delete</Button>
          </Modal.Body>
        </Modal>
      }
      <Container>
        <Row>
          <TimezoneSelect
            value={tz}
            onChange={(x) => setTZ(x.value)}
            styles={{ menu: provided => ({ ...provided, zIndex: 9999 }) }}
          />
        </Row>
        <Row>
          <FullCalendar
            headerToolbar={{ left: 'today', center: '', right: 'backward,forward' }}
            height="auto"
            contentHeight="auto"
            firstDay={2}
            plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin, momentTimezonePlugin]}
            themeSystem='bootstrap5'
            initialView="timeGridWeek"
            customButtons = {{
              backward: {
                text: '<',
                icon: 'chevron-left',
                click: function() {
                  calendar.current?.getApi().removeAllEvents();
                  calendar.current?.getApi().prev();
                }
              },
              forward: {
                text: '>',
                icon: 'chevron-right',
                click: function() {
                  calendar.current?.getApi().removeAllEvents();
                  calendar.current?.getApi().next();
                }
              },
            }}
            selectable={true}
            select={handleSelect}
            eventDrop={handleEditEvent}
            eventResize={handleEditEvent}
            eventClick={handleEventClick}
            slotMinTime="19:00:00"
            slotMaxTime="25:00:00"
            slotDuration="00:15:00"
            scrollTime="20:00:00"
            allDaySlot={false}
            eventOverlap={false}
            slotLabelFormat={{
              hour: '2-digit',
              minute: '2-digit',
              hour12: false,
              omitZeroMinute: false,
            }}
            eventTimeFormat={{
              hour: '2-digit',
              minute: '2-digit',
              hour12: false,
            }}
            editable
            events={`https://mist.pgw.jp:30000/schedule/show/${userId}`}
            timeZone={tz as string}
            ref={calendar}
          />
        </Row>
      </Container>
    </>
  );
};

export default UserProfile;
