import * as React from 'react';
import * as Router from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import fetcher from '../../fetcher';
import * as E from 'fp-ts/Either';
import { Badge, Col, Modal, Row } from 'react-bootstrap';
import { useAppDispatch, useAppSelector } from '../../hooks';
import { logIn, setUserId } from '../../features/user/user-slice';


const Content: React.FC<{}> = () => {
    const [name, setName] = React.useState("");
    const [password, setPassword] = React.useState("");
    const [errorMessage, setErrorMessage] = React.useState<string | null>(null);
    const roomId = useAppSelector((state) => state.roomR.roomId);
    const roomName = useAppSelector((state) => state.roomR.roomName);
    const dispatch = useAppDispatch();
    const handleName = (event: React.ChangeEvent<HTMLInputElement>) => {
        event.preventDefault();
        setName(event.target.value);
    };
    const handlePassword = (event: React.ChangeEvent<HTMLInputElement>) => {
        setPassword(event.target.value);
    };
    const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const login = fetcher.path('/schedule/login').method('post').create();
        login({ name: name, password: password, room_id: roomId })
            .then((res) => {
                dispatch(setUserId(res.data as number));
            }).catch((e) => {
                setErrorMessage(e.data);
            });
    };
    return (
        <Container>
            <Row>
                <Col><h1>Login to {roomName}</h1></Col>
            </Row>
            <Row>
                <Col>
                    <Form onSubmit={handleSubmit}>
                        <Form.Group controlId="name">
                            <Form.Label>Display Name for {roomName}</Form.Label>
                            <Form.Control type="text" placeholder="display name" onChange={handleName} />
                        </Form.Group>
                        <Form.Group controlId="password">
                            <Form.Label>Password <Badge>Optional</Badge></Form.Label>
                            <Form.Control type="password" placeholder="password (optional)" aria-describedby="passwordHelpBlock" onChange={handlePassword} maxLength={20} />
                            <Form.Text id="passwordHelpBlock" className="text-danger">
                                {errorMessage ? errorMessage : ""}
                            </Form.Text>
                        </Form.Group>
                        <Button type="submit" variant="primary">Login</Button>
                    </Form>
                </Col>
            </Row>
        </Container>
    )
};

export default Content;