import * as React from 'react';
import * as Router from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import fetcher from '../../fetcher';
import * as E from 'fp-ts/Either';
import { useAppDispatch, useAppSelector } from '../../hooks';
import { resolved, setRoomName } from '../../features/room/room-slice';

const LoadingScreen: React.FC<{}> = () => {
    const roomId = useAppSelector((state) => state.roomR.roomId);
    const dispatch = useAppDispatch();
    const findRoom = fetcher.path('/room/find/{room_id}').method('get').create();
    findRoom({ room_id: roomId }).then((res) => {
        dispatch(setRoomName(res.data as string));
    }).catch((err) => {
        dispatch(resolved());
    });
    return (
        <h1>Loading...</h1>
    );
};

export default LoadingScreen;