import React, { Component } from "react";
import { useLocation, Route, Routes, Navigate } from "react-router-dom";

import AdminNavbar from "../components/Navbars/AdminNavbar";
import Footer from "../components/Footer/Footer";
import Sidebar from "../components/Sidebar/Sidebar";
import FixedPlugin from "../components/FixedPlugin/FixedPlugin";

import {dashboardRoutes, DashboardRoute} from "../routes";

import sidebarImage from "../assets/img/sidebar-3.jpg";
import { useAppSelector } from "../hooks";

const Admin : React.FC<{}> = () => {
  const [image, setImage] = React.useState(sidebarImage);
  const [color, setColor] = React.useState("black");
  const [hasImage, setHasImage] = React.useState(true);
  const location = useLocation();
  const mainPanel = React.useRef<HTMLDivElement|null>(null);
  const roomId = useAppSelector((state) => state.roomR.roomId);
  const getRoutes = (routes : DashboardRoute[]) => {
    return dashboardRoutes.map((prop, key) => {
      if (prop.layout === "admin") {
        return (
          <Route
            path={`${prop.layout}${prop.path}`}
            element={<prop.component/>}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };
  React.useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement && (document.scrollingElement.scrollTop = 0);
    mainPanel && mainPanel.current && (mainPanel.current.scrollTop = 0);
    if (
      window.innerWidth < 993 &&
      document.documentElement.className.indexOf("nav-open") !== -1
    ) {
      document.documentElement.classList.toggle("nav-open");
      var element = document.getElementById("bodyClick");
      element?.parentNode?.removeChild(element);
    }
  }, [location]);
  return (
    <>
      <div className="wrapper">
        <Sidebar color={color} image={hasImage ? image : ""} routes={dashboardRoutes} />
        <div className="main-panel" ref={mainPanel}>
          <div className="content">
            <Routes>
              <Route index element={<Navigate to="admin/candidates"></Navigate>}></Route>
              {getRoutes(dashboardRoutes)}
            </Routes>
          </div>
        </div>
      </div>
      <FixedPlugin
        hasImage={hasImage}
        setHasImage={() => setHasImage(!hasImage)}
        color={color}
        setColor={(color) => setColor(color)}
        image={image}
        setImage={(image) => setImage(image)}
      />
    </>
  );
};

export default Admin;
