import { components } from "./schema";
import * as D from 'date-fns';
export type TimeRange = {
    userNames: string[];
    start: Date;
    end: Date;
}

export function contains(self: TimeRange, other: TimeRange): boolean {
    return !(D.isAfter(self.start, other.start) || D.isBefore(self.end, other.end));
}

export function startsBefore(self: TimeRange, other: TimeRange): boolean {
    return D.isBefore(self.start, other.start);
}

export function startsAfter(self: TimeRange, other: TimeRange): boolean {
    return D.isAfter(self.start, other.start);
}

export function endsBefore(self: TimeRange, other: TimeRange): boolean {
    return D.isBefore(self.end, other.end);
}

export function endsAfter(self: TimeRange, other: TimeRange): boolean {
    return D.isAfter(self.end, other.end);
}

export function isDisjoint(self: TimeRange, other: TimeRange): boolean {
    return !(D.isAfter(self.end, other.start) && D.isBefore(self.start, other.end));
}
