import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-icons/font/bootstrap-icons.css'; // needs additional webpack config!

import * as React from 'react';
import Container from 'react-bootstrap/Container';
import FullCalendar, { DateSelectArg, EventApi, EventClickArg, EventDropArg, EventSourceFunc } from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin, { EventResizeDoneArg } from '@fullcalendar/interaction'; // for selectable
import { useAppDispatch, useAppSelector } from '../hooks';
import fetcher from '../fetcher';
import * as TA from '../schedule-alg';
import * as D from 'date-fns';
import { max } from 'date-fns/esm';
import Gradient from "javascript-color-gradient";
import { Row, Modal } from 'react-bootstrap';
import TimezoneSelect, { ITimezone } from 'react-timezone-select';
import momentTimezonePlugin from '@fullcalendar/moment-timezone';
import moment from 'moment-timezone';
import { setEventId } from '../features/selection/selection-slice';


type EventInfoDialogProp = {
  calendar: React.MutableRefObject<FullCalendar | null>;
}

const EventInfoDialog: React.FC<EventInfoDialogProp> = (prop) => {
  const dispatch = useAppDispatch();
  const eventId = useAppSelector((state) => state.selectionR.eventId);
  const handleClose = () => dispatch(setEventId(null));
  return (
    <>
      {eventId &&
        <Modal show={true} onHide={handleClose} animation={false} backdrop='static' fullscreen='sm-down'>
          <Modal.Header closeButton>
            <Modal.Title>Event Information</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            {prop.calendar.current?.getApi().getEventById(eventId)?.title}
          </Modal.Body>
        </Modal>
      }
    </>
  );
};

const Dashboard: React.FC<{}> = () => {
  const dispatch = useAppDispatch();
  const [tz, setTZ] = React.useState<ITimezone>(Intl.DateTimeFormat().resolvedOptions().timeZone);
  const [eventClicked, setEventClicked] = React.useState<EventApi | null>(null);
  const calendar = React.useRef<FullCalendar | null>(null);
  const roomId = useAppSelector((state) => state.roomR.roomId);
  const getEvents = fetcher.path('/schedule/show_all/{room_id}').method('get').create();
  const fetchEvents: EventSourceFunc = (fetchInfo, successCallBack, failureCallback) => {
    getEvents({ room_id: roomId, start: fetchInfo.startStr, end: fetchInfo.endStr }).then((res) => {
      if (res.data.length <= 0) successCallBack([]);
      let userEvents: TA.TimeRange[] = res.data.map((e, i, a) => {
        return { userNames: [e.user_name], start: new Date(e.start), end: new Date(e.end) };
      });
      let cells: TA.TimeRange[] = [];
      for (let d = fetchInfo.start; !D.isAfter(d, fetchInfo.end); d = D.add(d, { minutes: 15 })) {
        let t: TA.TimeRange = { userNames: [], start: d, end: D.add(d, { minutes: 15 }) };
        cells.push(t);
      }
      cells.forEach((x, i, a) => {
        userEvents.forEach((e, j, b) => {
          if (!TA.isDisjoint(x, e)) {
            x.userNames.push(e.userNames[0]);
          }
        });
      });
      cells = cells.filter((x, i, a) => x.userNames.length > 0);
      let un = cells.map((x, i, a) => x.userNames.length);
      let maxUN = Math.max(...un);
      let minUN = Math.min(...un);
      let gradients = new Gradient().setColorGradient("ff0000", "00ff00").setMidpoint(maxUN).getColors();
      successCallBack(cells.map((x, i, a) => {
        let ul = x.userNames.length;
        return { id: `{event-${i}}`, start: x.start, end: x.end, title: `${ul}: ${[...x.userNames].join(' , ')}`, backgroundColor: gradients[ul - minUN] };
      }));
    }).catch((e) => {
      failureCallback(e);
    });
  };
  const handleEventClick = (e: EventClickArg) => {
    dispatch(setEventId(e.event.id));
  };
  return (
    <>
      <Container>
        <Row>
          <TimezoneSelect
            value={tz}
            onChange={(x) => setTZ(x.value)}
            styles={{ menu: provided => ({ ...provided, zIndex: 9999 }) }}
          />
        </Row>
        <Row>
          <FullCalendar
            firstDay={2}
            headerToolbar={{left: 'today', center: '', right: 'prev,next'}}
            height="auto"
            contentHeight="auto"
            plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin, momentTimezonePlugin]}
            themeSystem='bootstrap5'
            initialView="timeGridWeek"
            selectable={true}
            eventClick={handleEventClick}
            slotMinTime="19:00:00"
            slotMaxTime="25:00:00"
            slotDuration="00:15:00"
            scrollTime="20:00:00"
            allDaySlot={false}
            slotLabelFormat={{
              hour: '2-digit',
              minute: '2-digit',
              hour12: false,
              omitZeroMinute: false,
            }}
            eventTimeFormat={{
              hour: '2-digit',
              minute: '2-digit',
              hour12: false,
            }}
            expandRows={true}
            events={fetchEvents}
            timeZone={tz as string}
            ref={calendar}
          />
        </Row>
      </Container>
      <EventInfoDialog calendar={calendar} />
    </>
  );
}

export default Dashboard;
