import * as React from 'react';
import * as Router from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import fetcher from '../fetcher';
import * as E from 'fp-ts/Either';
import Content from './LoginForm/Content';
import LoadingScreen from './LoginForm/LoadingScreen';
import { useAppSelector } from '../hooks';
import { store } from '../store';


const LoginForm: React.FC<{}> = () => {
    const roomName = useAppSelector((state) => state.roomR.roomName);
    const loaded = useAppSelector((state) => state.roomR.resolved);
    return (
        loaded ? (
            roomName ? (<Content />) : (<Container><h1>The room is not found</h1></Container>)
        ) : (<LoadingScreen></LoadingScreen>)
    );
};

export default LoginForm;