import { EventApi } from '@fullcalendar/react';
import { createAction, createSlice, PayloadAction } from '@reduxjs/toolkit';

interface EventSelectionState {
    eventId: string | null;
}

const initialState: EventSelectionState = {
    eventId: null,
};

const eventSelectionSlice = createSlice({
    name: 'event-selection',
    initialState,
    reducers: {
        setEventId(state: EventSelectionState, action: PayloadAction<string|null>){
            state.eventId = action.payload;
        },
    }
});

export const {setEventId} = eventSelectionSlice.actions;
export default eventSelectionSlice.reducer;